@-webkit-keyframes onecart-summary-fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes onecart-summary-fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes onecart-summary-fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes onecart-summary-fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


.onecart .onecart-summary {
  position: absolute;
  min-width: 300px;
  background: white;

  -webkit-transform: translateX(-100%);

      -ms-transform: translateX(-100%);

          transform: translateX(-100%);
  -webkit-box-shadow: 0px 2px 5px 0px rgba(144, 143, 143, 0.7);
          box-shadow: 0px 2px 5px 0px rgba(144, 143, 143, 0.7);
  outline: none;

  -webkit-animation: onecart-summary-fade-in 300ms 100ms forwards ease-out;

          animation: onecart-summary-fade-in 300ms 100ms forwards ease-out;
  opacity: 0;
}

.onecart .onecart-summary.fading-out {
  -webkit-animation: onecart-summary-fade-out 300ms forwards ease-out;
          animation: onecart-summary-fade-out 300ms forwards ease-out;
}

.onecart .onecart-summary .list-group-item {
  padding: 0.25rem 0;
}

.onecart .onecart-summary .modal-body {
  padding: 0.5rem 1rem;
}

.onecart .onecart-summary .modal-footer {
  padding: 0;
}

.onecart .onecart-summary .modal-footer .btn {
  border-radius: 0;
}



